















import fIcon from '@/components/m-icon/f-icon.vue'
export default {
	components: {fIcon},
	data() {
		return{
			statement:''
		}
	},
	methods:{
		
	},
	onLoad() {
		uni.request({
			url: this.$http.contextPath + 'base/company-statement/getOne',
			method: 'POST',
			data:{
				status:'1',
				category:'ServiceAgreement'
			},
			success: (res) => {
				if(this.$http.success(res)){
					this.statement = res.data.data.statementText;
				}
			}
		})
	}
}
